// src/utils/politenessUtils.js
export function getPolitenessColor(politeness) {
    politeness = parseFloat(politeness);
    if (politeness >= 0 && politeness < 1) return '#3F51B5'; // indigo
    if (politeness >= 1 && politeness < 2) return '#03A9F4'; // light-blue
    if (politeness >= 2 && politeness < 3) return '#4CAF50'; // green
    if (politeness >= 3 && politeness < 4) return '#FF9800'; // orange
    if (politeness >= 4) return '#F44336'; // red
    return 'grey';
  }
  